.link{
    color: white;
    text-decoration: none;
}

.Navbar{
    background-color: rgb(34, 34, 34);
    height: 60px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: 1px 3px 14px green;
}

.left{
    font-size: 2em;
    gap: 12px;
    display: flex;
    align-items:center ;
    justify-content: center;
    color: rgb(1, 204, 1);
}

.nav-home{
    font-size: 17px;
    font-weight: normal;
}

.right{
    font-size: 1.2em;
    font-weight: bold;
    display: flex;
    /* align-items: center; */
    justify-content: center;
    gap: 1em;
}

.cart-symbol{
    position: relative;
}

.cart-len{
    position: absolute;
    background: rgb(2, 179, 2);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1px;
    border-radius: 50%;
    top: -7px;
    left: 12px;
    width: 19px;
    font-size: 13px;
    animation: trans;
    animation-duration: 0.6s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.cart-symbol::selection .cart-len{
  display: none;
}

@keyframes trans{
    0%{
       transform: translateY(0);
    }
    25%{
        transform: translateY(2px);
    }
    50%{
        transform: translateY(0);
    }
    75%{
        transform: translateY(-2px);
    }
    100%{
        transform: translateY(0);
    }
}
