.cart-item{
    display: flex;
    border-bottom: 2px solid rgb(82, 82, 82);
    margin: 1em 8px;
    padding: 1em 2em;
    text-align: start;
    gap: 4em;
}

.cart-img{
    width: 120px;
}

.cart-item-desc{
    font-size: 14px;
    color: rgb(102, 101, 101);
    font-weight: 500;
    margin: 0.5em 0;
}

.price-del-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.item-price{
    font-weight: bold;
    font-size: 17px;
    color: rgb(0, 161, 0);
}

.delete-btn{
    background-color: rgb(252, 170, 170);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    color: rgb(216, 2, 2);
    border-radius: 50%;
    cursor: pointer;
}