.cart-container{
    width: 70%;
    /* border: 2px solid green; */
    margin: 2em auto;
    display: flex;
    justify-content: space-between;
    gap:5px ;
}

.cart-left{
    width: 55%;
}

.cart-container-right{
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: space-between;
    margin: 3em 0;
    text-align: start;
}

.your-cart{
    font-size: 19px;
    font-weight: bold;
    color: rgb(0, 161, 0);
    text-transform: uppercase
}

.cart-container-right div h1{
    color: rgb(0, 161, 0);
    margin: -6px 0 10px 0;
}

.f-b{
    font-weight: bold;
}

.check-btn{
    width: 300px;
    text-align: center;
    padding: 10px 0;
    border: none;
    background-color:rgb(0, 139, 0) ;
    color: white;
    margin: 12px 0;
    font-size: 15px;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}

.no-item-container{
    display: flex;
    flex-direction: column;
    height: 100vh;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.no-item-container div{
    font-size: 17px ;
    font-weight: bold;
}

.shop-now-btn{
    border: none;
    padding: 10px 1em;
    background-color:rgb(0, 139, 0);
    color: white;
    font-weight: bold;
    font-size: 15px;
    border-radius: 4px;
    cursor: pointer;
}

.shop-now-btn:hover,.check-btn:hover{
    background: transparent;
    border: 1px solid rgb(0, 139, 0);
    color: rgb(0, 139, 0);
}