.product{
    width: 23%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 0.7em 10px;
    padding: 12px;
    border-radius: 8px;
    box-shadow: 2px 2px 8px rgb(196, 196, 196);
}

.product:hover{
    scale: 1.05;
    box-shadow: 6px 6px 40px rgb(153, 153, 153);
    transition: all 0.2s;
    transition-timing-function: ease-in-out;
}

.btn-price{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.product-img{
    width: 100px;
    height: 150px;
    margin: 12px 0;
}

.price{
    font-size: 17px;
    font-weight: bold;
    color: rgb(0, 172, 0);
}

.cart-btn{
    border: 2px solid rgb(94, 94, 94);
    padding: 7px 10px;
    border-radius: 18px;
    background: transparent;
    font-size: 12px;
    color: rgb(83, 83, 83);
    font-weight: 600;
}

.product:hover  .cart-btn{
    background-color:rgb(83, 83, 83);
    color: white;
    cursor: pointer;
}

.product-title{
    margin-bottom: 8px;
    color: rgb(63, 63, 63);
}

.product-desc{
    font-size: 14px;
    color: rgb(160, 160, 160);
}